.content {
    width: 100%;
    &__icons {
        background-color: #fff; }
    &__icon {
        width: 25px;
        height: 25px;
        border: 1px solid #fff;
        &:hover, &.is-active {
            border: 1px solid $border-gray;
            .icon__rect-white {
                @include s(rect-gray); }
            .icon__lines-white {
                @include s(lines-gray); } } }
    &__table {
        width: 100%;
        background-color: #fff; }
    &__table-col_left {
        width: percentage(495/715) - 1%;
        display: inline-block; }
    &__table-col_right {
        width: percentage(220/715) - 1%;
        display: inline-block; }
    &__table-head {
        border-bottom: 1px solid $border-gray;
        height: 26px;
        margin: 0 14px; }
    &__head-elem {
        width: calc(100% / 3  - 1%);
        height: 26px;
        display: inline-block;
        font-size: em(10, 13); }
    &__head-elem_title {
        padding-left: 40px;
        display: inline-block;
        vertical-align: middle; }
    &__icon_head-elem {
        width: 20px;
        height: calc(100% - 1px);
        vertical-align: middle; }
    &__table-row {
        height: 60px;
        border-bottom: 1px solid $border-gray;
        margin: 0 14px; }
    &__table-cell {
        width: calc(100% / 3  - 1%);
        display: inline-block;
        vertical-align: middle;
        font-family: $font-regular;
        &_icon {
            width: percentage(32/495); }
        &_heading {
            width: percentage(455/495); } }
    &__text_table-cell {
        font-family: $font-common;
        font-size: em(10, 13); }
    &__link_table-cell {
        font-size: em(15, 13); }
    &__container {
        position: relative;
        &:hover {
            background-color: $hover-gray;
            .content__table-row {
                border: none; }
            .content__table-cell_heading, .content__link_table-cell {
                color: #fff; } } }
    &__pop-up {
        position: absolute;
        bottom: 0;
        right: 0;
        width: percentage(255/715);
        height: 100%; }
    &__icon_pop-up {
        background: $hover-gray;
        width: 25%;
        height: 100%;
        text-align: center;
        &:hover {
            background: $bg-dark; } }

    &__table[data-style="rows"] {
        padding-bottom: 26px;
        .content__table-row {
            @include vertical;
            font-size: 1em;
            text-align: left; } }

    &__table[data-style="columns"] {
        .content__table-head {
            display: none; }
        .content__table-col_left {
            width: 100%;
            height: 90%;
            display: block; }
        .content__table-col_right {
            width: 100%;
            height: 10%;
            display: block;
            text-align: center; }
        .content__container {
            width: calc(100% / 3 - 2%);
            min-width: 250px;
            min-height: 160px;
            display: inline-block;
            vertical-align: top;
            margin: 20px 5px;
            &:hover {
                .content__table-row {
                    border: none; } } }
        .content__table-row {
            border-top: 1px solid $border-gray;
            padding: 10px 0;
            min-height: 160px; }
        .content__table-cell {
            &_icon {
                vertical-align: top;
                width: 15%; }
            &_heading {
                width: 83%; } }
        .content__pop-up {
            position: absolute;
            bottom: 0;
            right: 0;
            width: 100%;
            height: 30%; } } }





